<template>
    <el-container>
        <el-header style="height: 30px">
            <div class="desc">安全性高的密码可以使帐号更安全。建议您定期更换密码，设置一个包含大小写字母，符号或数字中至少两项的密码。</div>
            <el-button @click="updatePwdHandleClick()" type="primary" size="mini" icon="el-icon-lock">修改密码</el-button>
        </el-header>
        <hr/>
        <el-main>
            <div class="desc">登录日志</div>
            <el-row>
                <el-col>
                    <el-table
                            :data="tableData"
                            border
                            stripe
                            style="width: 100%;">

                        <el-table-column v-if="nameShow"
                                         prop="name"
                                         label="用户名">
                        </el-table-column>


                        <el-table-column
                                prop="createDate"
                                label="登录时间">
                        </el-table-column>

                        <el-table-column
                                prop="ip"
                                label="登录IP">
                        </el-table-column>

                        <el-table-column
                                prop="address"
                                label="登录地点">
                        </el-table-column>
                    </el-table>
                </el-col>
            </el-row>
            <el-row>
                <el-pagination
                        @current-change="handleCurrentChange"
                        :current-page.sync="currentPage"
                        :page-size="pageSize"
                        layout="total, prev, pager, next"
                        :total="countSize">
                </el-pagination>
            </el-row>
        </el-main>
    </el-container>
</template>

<script>
    export default {
        data() {
            return {
                nameShow: false,
                tableData: null,
                currentPage: 1,
                countSize: 0,
                pageSize: 1
            }
        },
        mounted() {
            this.reload();
        },
        methods: {
            handleCurrentChange(val) {
                this.currentPage = val;
                this.reload();
            },
            updatePwdHandleClick() {
                this.$router.push({path: '/userManagePwd/index'});
            },
            reload() {
                this.axios.get("/api/loginLog/page?page=" + this.currentPage).then((response) => {
                    this.tableData = response.data.data.content;
                    this.currentPage = response.data.data.number + 1;
                    this.countSize = response.data.data.totalElements;
                    this.pageSize = response.data.data.size;
                    if (this.tableData) {
                        this.nameShow = this.tableData[0].name != undefined;
                    }
                }).catch((response) => {
                    console.log(response);
                })
            }
        }
    }
</script>

<style scoped>
    .desc {
        font-size: 12px;
        color: #999;
        padding-bottom: 10px;
    }
</style>
